import React from "react"
import { graphql } from "gatsby"
import Image from "gatsby-image"
import { css } from "@emotion/react"

import Layout from "../Layout/Layout"

export const query = graphql`
  query($slug: String!) {
    allDatoCmsPost(filter: { slug: { eq: $slug } }) {
      nodes {
        titulo
        contenido
        imagen {
          fluid(maxWidth: 1200) {
            ...GatsbyDatoCmsFluid
          }
        }
      }
    }
  }
`

const PostDetail = ({
  data: {
    allDatoCmsPost: { nodes },
  },
}) => {
  const {
    titulo,
    contenido,
    imagen: { fluid },
  } = nodes[0]
  return (
    <Layout>
      <div
        css={css`
          padding: 0 10px;
          text-align: justify;
          @media (min-width: 767px) {
            .image {
              margin: 10px auto 0;
              width: 80vw;
            }
          }
        `}
      >
        <Image fluid={fluid} className="image" />
        <h1>{titulo}</h1>
        <p dangerouslySetInnerHTML={{ __html: contenido }} />
      </div>
    </Layout>
  )
}

export default PostDetail
